$red:#ff1616;
$blue:#004aad;
$green:#32e650;
$purple:#9218ba;
$light-blue:#38b6ff;

.team-wrapper{
    flex:1;
    // background-color: navy;
    top:0;
    position: relative;
    max-height: 500px;
    width:30vw ;
    max-width: 1150px;
    margin: auto;
}
.team-top{
    height: 50%;
    // background-color: green;
    display: flex;
    justify-content: space-evenly;
    width: 1110px;
    max-width: 75vw;
    margin: auto;
}
.team-top-left{
    position: relative;
    img{
        border:5px solid $blue;
    }
    // background-color: gold;
    height: 249px;

}
.team-top-left-border{
    display: flex;
    align-items: center;
    position: absolute;
    height: 140px;
    width: 35px;
    border-top: 4px solid $blue;
    border-bottom: 4px solid $blue;
    left:-35px;
    top:50px;
    .team-name{
        position: relative;
        left: -10px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        color: black;
        height: 140%;
        background-color: $blue;
        border-radius: 10px;
        width: 80%; 
    }
}
.team-top-left-bottom-border{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    height: 40px;
    width: 180px;
    border-left: 4px solid $blue;
    border-right: 4px solid $blue;
    border-bottom: 4px solid $blue;
    margin: auto;
    left:0;
    right:0;
    bottom:0px;
    .team-role{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        color: black;
        height: 39px;
        background-color: $blue;
        border-radius: 10px;
        width: 90%; 
        bottom: -25px;
    }
}
.team-top-right{
    position: relative;
    img{
        border:5px solid $red;
        position: relative;
        z-index: 2;
    }
    height: 240px;
}

.team-top-right-border{
    display: flex;
    align-items: center;
    position: absolute;
    height: 210px;
    width: 45px;
    border-top: 4px solid $red;
    border-bottom: 4px solid $red;    
    right:-45px;
    top:20px;
    .team-name{
        position: relative;
        right: -30px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        color: black;
        height: 125%;
        background-color: $red;
        border-radius: 10px;
        width: 70%;
        z-index: 2; 
    }
    .rogue-border{
        position: absolute;
        height: 50px;
        width: 50px;
        border-bottom:4px solid $red ;
        margin-top: 44px;
    }
}
.team-top-right-bottom-border{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    height: 60px;
    width: 245px;
    border: 4px solid $red;
    margin: auto;
    left:-18px;
    right:0;
    bottom:-10px;
    background-color: black;
    .team-role{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        color: black;
        height: 39px;
        background-color: $red;
        border-radius: 10px;
        width: 90%; 
        bottom: -30px;
    }
  
}

.team-top-img{
    height: 210px;
    border-radius: 15px;
}
.team-bottom{
    position: relative;
    display: flex;
    height: 50%;
    width: 1100px;
    max-width: 75vw;
    margin: auto;
    margin-top: 100px;
}
.team-bottom-role{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 0;
    right:0;
    margin: auto;
    height: 54px;
    width: 600px;
    background-color: #ffde59;
    border-radius: 17px;
    top:-28px;
    z-index: 2;
    max-width: 75vw;
    font-size: 2rem;
}
.team-bottom-img{
    height: 195px;
    border-radius: 15px;
}
.team-bottom-right{
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    position: relative;
    border-top:5px solid $light-blue;
    border-right: 5px solid $light-blue;
    .team-bottom-img{
        border:5px solid $light-blue;
        position: relative;
        z-index: 2;
    }
    .team-social-name{
        background-color: $light-blue;
    }
    height: 270px;
    width:  33.33%;
}
.team-bottom-left{
    display: flex;
    align-items: flex-end;
    position: relative;
    border-top:5px solid $green;
    border-left: 5px solid $green;
    .team-bottom-img{
        border:5px solid $green;
        position: relative;
        z-index: 2;
    }
    .team-social-name{
        background-color: $green;
    }
    height: 270px;
    width: 33.33%;
}
.team-bottom-middle{
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: center;
    position: relative;
    .team-bottom-img{
        border:5px solid $purple;
        position: relative;
        z-index: 2;
    }
    .middle-border{
        flex:1;
        border-right: 5px solid $purple;
    }
    .team-social-name{
        background-color: $purple;
    }
    height: 270px;
    width:  33.33%;
}
.team-bottom-left-inner,.team-bottom-right-inner,.team-bottom-middle-inner{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    top:10%;
    width: 195px;
    height: 110%;
}
.team-bottom-left-inner{
    left: -100px;
}
.team-bottom-right-inner{
    right: -100px;
}
.team-social-div{
    margin-top: 15px;
    display: flex;
    img{
        height: 33px;
        cursor: pointer;
    }
}
.team-social-name{
    display: flex;
    align-items: center;
    padding: 1px 5px 5px 5px;
    height: 30px;
    border-radius: 12px;
    margin-left: 5px;
}
@media screen and (max-width:930px) {
    .team-wrapper{
        margin-top: 3rem;
    }
    .team-bottom{
        flex-direction: column;
        align-items: center;
    }
    .team-bottom-left-inner,.team-bottom-right-inner,.team-bottom-middle-inner{
    
        position: unset;
    }
    .team-bottom-right,.team-bottom-left,.team-bottom-middle{
        display: flex;
        justify-content: center;
        border:unset;
        margin-top: 4rem;
    }
    .team-social-div{
        position: relative;
        left: -5px;
    }
    .team-bottom-role{
        font-size: 1.3rem ;
    }
}
@media screen and (max-width:630px) {
   
    .team-top{
        flex-direction: column;
        align-items: center;
        height: fit-content;
        margin-top: 3rem;
        max-width: 100vw;
    }
    .team-top-left,.team-top-right{
        margin-top: 5rem;
    }
}

@media screen and (max-width:505px) {
   
    .team-bottom-role{
        font-size: 3.9vw;
    }
}